import React from "react";
import { Navbar, Container, Nav } from "react-bootstrap";
import { Link } from "react-scroll";
import styled from "styled-components";

const NavBar = styled(Navbar)`
	height: 80px;
	-webkit-box-shadow: 0px 10px 7px -8px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 10px 7px -8px rgba(0, 0, 0, 0.75);
`;

const LinkStyled = styled(Link)`
	text-decoration: none;
	cursor: pointer;
	width: 110px;
	color: white;
	font-size: 100%;

	&:hover {
		color: gold;
		font-size: 110%;
	}
`;

const Title = styled.h3`
	color: black;
	font-weight: bold;
	background-color: white;
	padding: 10px;
	cursor: pointer;
	border-radius: 15px 0 15px 0;
	border-bottom: 2px solid black;
`;

export default function Header() {
	return (
		<NavBar bg="success" sticky="top">
			<Container className="d-flex flex-row">
				<a
					to="intro-scroll"
					smooth={true}
					duration={1000}
					offset={-300}
				>
					<Title>Shacosu.js</Title>
				</a>
				<Navbar.Toggle aria-controls="basic-navbar-nav" />
				<Navbar.Collapse id="basic-navbar-nav">
					<Nav className="ms-auto">
						<LinkStyled
							to="about-scroll"
							smooth={true}
							duration={1000}
							offset={-300}
						>
							<i className="fas fa-info-circle"></i> Sobre Mi
						</LinkStyled>

						<LinkStyled
							to="proyect-scroll"
							smooth={true}
							offset={-260}
							duration={1000}
						>
							<i className="fas fa-bookmark "></i> Proyectos
						</LinkStyled>
						<LinkStyled
							to="contact-scroll"
							smooth={true}
							offset={-250}
							duration={1000}
						>
							<i className="fas fa-envelope "></i> Contacto
						</LinkStyled>
					</Nav>
				</Navbar.Collapse>
			</Container>
		</NavBar>
	);
}
