import React, { useRef, useState } from "react";
import { Container } from "react-bootstrap";

import styled from "styled-components";
import emailjs from "emailjs-com";
import Swal from "sweetalert2";

const Label = styled.label`
	margin: 18px 0;
	font-weight: 600;
`;

const ContainerStyled = styled(Container)`
	margin: 0 0 150px;
	padding: 0;
	max-height: 650px;
`;

const ButtonStyled = styled.button`
	background-color: #9b9b9b;
	color: black;
	border: none;
	font-weight: bold;
	font-size: 12px;
	transition: 75ms;
	width: 80%;
	height: 45px;
	border-radius: 25px;
	margin: auto;

	&:hover {
		background-color: #DC3545;
		color: white;
	}
`;

export default function Contact() {
	const [name, setName] = useState("");
	const [email, setEmail] = useState("");
	const [subject, setSubject] = useState("");
	const [message, setMessage] = useState("");

	const form = useRef();
	const handleSubmit = (e) => {
		e.preventDefault();
		if (name !== "" && email !== "" && subject !== "" && message !== "") {
			emailjs
				.sendForm(
					"service_spm6n5d",
					"template_rhbbiji",
					form.current,
					"user_TSnPVC5E2IQqLQUH8M0A5"
				)
				.then(
					() => {
						Swal.fire({
							position: "middle",
							icon: "success",
							title: "El mensaje se ha enviado con exito!",
							showConfirmButton: false,
							timer: 1500,
						});
					}
				).catch(() => {
					Swal.fire({
						position: "middle",
						icon: "error",
						title: `No se ha podido enviar el mensaje.`,
						showConfirmButton: false,
						timer: 1500,
					});
				});
		} else {
			return Swal.fire({
				position: "middle",
				icon: "warning",
				title: `Todos los campos deben estar completos.`,
				showConfirmButton: false,
				timer: 1500,
			});
		}
		e.target.reset();
	};
	return (
		<ContainerStyled
			
			className="d-flex justify-content-center align-items-center mx-auto p-5 w-50"
			id="contact-scroll"
		>
			<form className="row " ref={form} onSubmit={handleSubmit}>
				<Label htmlFor="emailForm" className="px-0 text-uppercase text-white ">
					<i className="fas fa-user"></i> Nombre
				</Label>
				<input
					type="text"
					placeholder="Nombre..."
					name="user_name"
					className="px-3 p-2 rounded border-0 "
					onChange={(e) => setName(e.target.value)}
				/>
				<Label htmlFor="emailForm" className="px-0  text-uppercase text-white">
					<i className="fas fa-at"></i> Email
				</Label>
				<input
					type="email"
					placeholder="Email..."
					name="user_email"
					className="px-3 p-2 rounded border-0"
					onChange={(e) => setEmail(e.target.value)}
				/>
				<Label
					htmlFor="emailForm"
					className="p-0 px-0 text-uppercase text-white"
				>
					<i className="fas fa-paperclip"></i> Asunto
				</Label>
				<input
					type="text"
					placeholder="Asunto..."
					name="user_subject"
					className="px-3 p-2 rounded border-0"
					onChange={(e) => setSubject(e.target.value)}
				/>
				<Label
					htmlFor="emailForm"
					className="p-0 px-0 text-uppercase text-white"
				>
					<i className="fas fa-pen"></i> Dejar un mensaje
				</Label>
				<textarea
					type="text"
					placeholder="Mensaje..."
					name="message"
					rows="4"
					className="px-3 p-2 rounded border-0"
					onChange={(e) => setMessage(e.target.value)}
				/>
				<ButtonStyled type="submit" className="mt-4">
					Enviar
				</ButtonStyled>
				<p className="mt-4 text-center text-white">
					¬°Con gusto responder√© todas tus dudas y consultas! :)
				</p>
			</form>
		</ContainerStyled>
	);
}
